import { useI18n } from 'vue-i18n'
import type { FormRules, FormItemRule } from 'element-plus'
import { InternalRuleItem, SyncValidateResult } from 'async-validator/dist-types/interface'

export class FormRuleBuilder {
  private rules: FormRules
  private currentField: string
  private locale

  constructor () {
    this.rules = {}
    this.currentField = ''
    this.locale = useI18n()
  }

  field (name: string) {
    this.currentField = name

    if (!this.rules[name]) {
      this.rules[name] = []
    }

    return this
  }

  required (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      required: true,
      message: this.locale.t('main.rules.required'),
      trigger
    })

    return this
  }

  decimal (required = false, trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    const validator = function (rule: InternalRuleItem, value: string): SyncValidateResult | void {
      return (/^[-+]?[0-9]+\.[0-9]+$/).test(value)
    }

    this.setRuleItem({
      required,
      type: 'string',
      message: this.locale.t('main.rules.decimal'),
      validator,
      trigger
    })

    return this
  }

  phone (required = false, trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    const validator = function (rule: InternalRuleItem, value: string): SyncValidateResult | void {
      return (/^[\\+]?[(]?[0-9]{3}[)]?[-\s\\.]?[0-9]{3}[-\s\\.]?[0-9]{4,6}$/).test(value)
    }

    this.setRuleItem({
      required,
      type: 'string',
      message: this.locale.t('main.rules.phone'),
      validator,
      trigger
    })

    return this
  }

  length (min = 0, max = 255, trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      min,
      max,
      message: this.locale.t('main.rules.length', [min, max]),
      trigger
    })

    return this
  }

  minLength (min = 0, trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      min,
      message: this.locale.t('main.rules.minLength', [min]),
      trigger
    })

    return this
  }

  maxLength (max = 255, trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      max,
      message: this.locale.t('main.rules.maxLength', [max]),
      trigger
    })

    return this
  }

  enum (values: Array<string | number | boolean | null | undefined>, trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'enum',
      enum: values,
      message: this.locale.t('main.rules.enum'),
      trigger
    })

    return this
  }

  boolean (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'boolean',
      message: this.locale.t('main.rules.boolean'),
      trigger
    })

    return this
  }

  date (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'date',
      message: this.locale.t('main.rules.date'),
      trigger
    })

    return this
  }

  url (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'url',
      message: this.locale.t('main.rules.url'),
      trigger
    })

    return this
  }

  email (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'email',
      message: this.locale.t('main.rules.email'),
      trigger
    })

    return this
  }

  number (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'number',
      message: this.locale.t('main.rules.number'),
      trigger
    })

    return this
  }

  string (trigger = 'blur') {
    if (!this.checkField()) {
      return this
    }

    this.setRuleItem({
      type: 'string',
      message: this.locale.t('main.rules.string'),
      trigger
    })

    return this
  }

  end () {
    return this.rules
  }

  private setRuleItem (ruleItem: FormItemRule) {
    if (Array.isArray(this.rules[this.currentField])) {
      (this.rules[this.currentField] as unknown as any[]).push(ruleItem)
    } else {
      this.rules[this.currentField] = ruleItem
    }
  }

  private checkField () {
    if (!this.currentField) {
      return false
    }

    if (!this.rules[this.currentField]) {
      this.rules[this.currentField] = []
    }

    return true
  }
}

export class FormRuleTemplateBuilder {
  private builder: FormRuleBuilder

  constructor (builder: FormRuleBuilder) {
    this.builder = builder
  }

  title (max = 255) {
    this.builder.field('title').required().string().maxLength(max)

    return this
  }

  name (max = 64) {
    this.builder.field('name').required().string().maxLength(max)

    return this
  }

  isoCode2 (max = 2) {
    this.builder.field('isoCode2').string().length(max, max)

    return this
  }

  isoCode3 (max = 3) {
    this.builder.field('isoCode3').string().length(max, max)

    return this
  }

  image (required = false, max = 255) {
    this.builder
      .field('image')
      .string('change')
      .maxLength(max, 'change')

    if (required) {
      this.builder.required('change')
    }

    return this
  }

  code (max = 10, required = true) {
    this.builder.field('code').string().maxLength(max)

    if (required) {
      this.builder.required()
    }

    return this
  }

  fromName (max = 64) {
    this.builder.field('fromName').required().string().maxLength(max)

    return this
  }

  toName (max = 64) {
    this.builder.field('toName').required().string().maxLength(max)

    return this
  }

  message (max = 64) {
    this.builder.field('message').required().string().maxLength(max)

    return this
  }

  amount () {
    this.builder.field('amount').required().decimal()

    return this
  }

  voucherThemeId () {
    this.builder.field('voucherThemeId').required().number()

    return this
  }

  toEmail (max = 96) {
    this.builder.field('toEmail').required().email().maxLength(max)

    return this
  }

  isActive () {
    this.builder.field('isActive').required().boolean()

    return this
  }

  fromEmail (max = 96) {
    this.builder.field('fromEmail').required().email().maxLength(max)

    return this
  }

  firstName (max = 64) {
    this.builder.field('firstName').required().string().maxLength(max)

    return this
  }

  lastName (max = 64) {
    this.builder.field('lastName').required().string().maxLength(max)

    return this
  }

  middleName (max = 64) {
    this.builder.field('middleName').string().maxLength(max)

    return this
  }

  customerGroupId (required = true) {
    this.builder.field('customerGroupId').number('change')

    if (required) {
      this.builder.required('change')
    }

    return this
  }

  countryId (required = true) {
    this.builder.field('countryId').number('change')

    if (required) {
      this.builder.required('change')
    }

    return this
  }

  zoneId (required = true) {
    this.builder.field('zoneId').number('change')

    if (required) {
      this.builder.required('change')
    }

    return this
  }

  email (max = 96) {
    this.builder.field('email').required().email().maxLength(max)

    return this
  }

  phone (max = 32) {
    this.builder.field('phone').required().string().maxLength(max)

    return this
  }

  postcode (max = 10) {
    this.builder.field('postcode').string().maxLength(max)

    return this
  }

  city (max = 128) {
    this.builder.field('city').required().string().maxLength(max)

    return this
  }

  address (field = 'address', required = false, max = 128) {
    this.builder.field(field).string().maxLength(max)

    if (required) {
      this.builder.required()
    }

    return this
  }

  company (required = false, max = 64) {
    this.builder.field('company').string().maxLength(max)

    if (required) {
      this.builder.required()
    }

    return this
  }

  password (required = false, min = 6, max = 32) {
    this.builder
      .field('password')
      .string()
      .length(min, max)

    if (required) {
      this.builder.required()
    }

    return this
  }

  unit (required = false, max = 4) {
    this.builder.field('unit').string()

    if (required) {
      this.builder.required()
    }

    if (max) {
      this.builder.maxLength(max)
    }

    return this
  }

  value (required = false) {
    this.builder.field('value').string()

    if (required) {
      this.builder.required()
    }

    return this
  }

  metaDescription (required = false) {
    this.builder.field('metaDescription').string().maxLength(255)

    if (required) {
      this.builder.required()
    }

    return this
  }

  metaKeyword (required = false) {
    this.builder.field('metaKeyword').string().maxLength(255)

    if (required) {
      this.builder.required()
    }

    return this
  }

  seoUrl (required = false) {
    this.builder.field('seoUrl').string().maxLength(255)

    if (required) {
      this.builder.required()
    }
    return this
  }

  metaTitle (required = false) {
    this.builder.field('metaTitle').string().maxLength(255)

    if (required) {
      this.builder.required()
    }
    return this
  }

  location (required = false) {
    this.builder.field('location').string().maxLength(128)

    if (required) {
      this.builder.required()
    }

    return this
  }

  description (required = false, max: number | undefined = undefined) {
    this.builder.field('description').string()

    if (required) {
      this.builder.required()
    }

    if (max) {
      this.builder.maxLength(max)
    }

    return this
  }

  points () {
    this.builder.field('points').required().number()

    return this
  }

  sortOrder (required = false) {
    this.builder.field('sortOrder').number()

    if (required) {
      this.builder.required()
    }

    return this
  }

  price () {
    this.builder.field('price').required().decimal()

    return this
  }

  close () {
    return this.builder.end()
  }
}

export class FormRule {
  static builder () {
    return new FormRuleBuilder()
  }

  static templates () {
    return new FormRuleTemplateBuilder(
      this.builder()
    )
  }
}

export const isRequired = function () {
  return (value: any) => {
    if (typeof value === 'string') {
      return !!value.length || 'Данное поле обязательно!'
    }

    return !(value === null || value === undefined) || 'Данное поле обязательно!'
  }
}

export const isInteger = function () {
  return (value: unknown) => {
    return Number.isInteger(value) || 'Только целые числа!'
  }
}

export const minLength = function (length: number) {
  return (v: string) => (v.length > length || `Минимальное количество символов ${length}!`)
}

export const maxLength = function (length: number) {
  return (v: string) => (v.length < length || `Минимальное количество символов ${length}!`)
}

export const isEmail = function () {
  const re = /^[\w-\]+@[\w-]+\.[a-z]{2,4}$/i
  return (v: string) => (re.test(v) || 'Введите корректную почту')
}
export const isPhone = function () {
  const re = /^((8|\+7)[ \- ]?)?(\(?\d{3}\)?[ \- ]?)?[\d\- ]{7,10}$/
  return (v: string) => (re.test(v) || 'Введите корректный номер телефона')
}

export const customerFormRules = {
  fistName: [
    isRequired(),
    maxLength(64)
  ],

  lastName: [
    isRequired(),
    maxLength(64)
  ],

  email: [
    isRequired(),
    isEmail()
  ],

  phone: [
    isRequired(),
    isPhone()
  ],

  password: [
    isRequired(),
    minLength(6),
    maxLength(64)
  ]
}
