
import { defineComponent, PropType, reactive, ref } from 'vue'
import type { FormRules } from 'element-plus'
import { IResponseTable, IVoucherForm, IVoucherHistoryTableItem, IVoucherTableItem } from '@/types'
import FormMixin from '@/mixins/form.mixin'
import { FormRule } from '@/constants/validators'
import { Header, ServerOptions } from 'vue3-easy-data-table'
import { useStore } from 'vuex'
import useFetching, { FetchStatus } from '@/hooks/useFetching'

export interface IData {
  model: IVoucherForm;
  isValid: boolean;
  headers: Header[];
  serverOptions: ServerOptions;
}

export default defineComponent({
  name: 'VoucherForm',

  mixins: [FormMixin],

  setup () {
    const store = useStore()
    const [loadTable, status, error] = useFetching((payload) => {
      return store.dispatch('voucher/loadTableHistory', payload)
    })
    return {
      loadTable,
      status,
      error,
      form: ref(),
      rules: reactive<FormRules>(
        FormRule
          .templates()
          .code(10)
          .fromName()
          .fromEmail()
          .toName()
          .toEmail()
          .voucherThemeId()
          .amount()
          .isActive()
          .close()
      )
    }
  },

  props: {
    modelValue: Object as PropType<IVoucherForm>,

    isLoading: Boolean as PropType<boolean>,

    isNew: {
      type: Boolean,
      default: false
    }
  },

  watch: {
    serverOptions: {
      handler: function (value) {
        this.loadServerSide(value)
      },
      deep: true
    },
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        code: '',
        fromName: '',
        fromEmail: '',
        toName: '',
        toEmail: '',
        voucherThemeId: 0,
        message: '',
        amount: '',
        isActive: true
      },

      headers: [
        { text: 'id', value: 'id', sortable: true },
        { text: this.$t('main.labels.orderNum'), value: 'orderId', sortable: true },
        { text: this.$t('main.labels.toName'), value: 'toName', sortable: true },
        { text: this.$t('main.labels.amount'), value: 'amount', sortable: true },
        { text: this.$t('main.labels.createdAt'), value: 'createdAt', sortable: true }
      ],
      serverOptions: {
        page: 1,
        rowsPerPage: 5,
        sortBy: 'id',
        sortType: 'desc'
      },
      isValid: false
    }
  },
  methods: {
    loadServerSide (options: ServerOptions) {
      const payload: any = {
        limit: options.rowsPerPage,
        offset: (options.page - 1) * options.rowsPerPage
      }

      if (options.sortBy && options.sortType) {
        payload.order = `${options.sortBy}:${options.sortType}`
      }

      this.loadTable(payload)
    }
  },
  mounted () {
    this.loadServerSide(this.serverOptions)
  },
  computed: {
    vouchers () {
      return this.$store.getters['voucherTheme/getItems']
    },
    isLoadingTable (): boolean {
      return this.status === FetchStatus.pending
    },

    dataTable (): IResponseTable<IVoucherHistoryTableItem> {
      return this.$store.getters['voucher/getDataTableHistory']
    }
  }
})
